:root {
    --agrofa-text: #12110e;
    --agrofa-text-rgb: 18, 17, 14;

    --agrofa-white: #083314;
    --agrofa-white-rgb: 8, 51, 20;

    --agrofa-base: #c2a74e;
    --agrofa-base-rgb: 194, 167, 78;
    --agrofa-black: #fff;
    --agrofa-black-rgb: 255, 255, 255;
    --agrofa-black3: #000;
    --agrofa-black3-rgb: 0, 0, 0;
    --agrofa-black4: #121013;
    --agrofa-black4-rgb: 18, 16, 19;
    --agrofa-gray: #1c1a1d;
    --agrofa-gray-rgb: 28, 26, 29;
    --agrofa-border-color: #2f2d30;
    --agrofa-border-color-rgb: 47, 45, 48;
}

.topbar-one__social { border-color: #11441f; }
